#account-view {

  .item.bar {
    height: 60px;
    button {
      transition: all ease 0.5s;
      color: #999;
      &.active {
        color: $positive;
        opacity: 1;
      }
    }
  }

  .scroll-content {
    background-color: #fff;
  }

  ion-tabs.tabs-light.tabs-top.tabs-bottom.tabs-standard {
    display: block;
    position: relative;
    height: 100%;
  }

  .tabs-top > .tabs, .tabs.tabs-top {
    top: 0;
    padding-top: 0;
    background-position: bottom;
    border-top-width: 1px;
    border-bottom-width: 1px;

    a {
      color: $positive;
    }
  }

  .view-container {
    display: block;
    position: relative;
    top: 48px;
  }

  .profile-top {
    display: block;
    position: relative;
    $avatar-size: 85px;
    img {
      display: inline;
      min-width: $avatar-size;
      min-height: $avatar-size;
      max-width: $avatar-size;
      max-height: $avatar-size;
      border-radius: 50%;
      margin: 6px;
      background-color: #ddd;
    }

    .col-statics {
      margin-left: 5px;
      span {
        font-weight: bold;
        color: #000;
        display: block;
        text-align: center;
        font-size: 20px;
      }
      h3 {
        font-size: 14px;
        color: #888;
        margin: 5px 0px;
        display: block;
        font-variant: small-caps;
        text-align: center;
        text-transform: lowercase;
        white-space: nowrap;
      }
    }

    .col-edit {
      padding: 0;
      .button {
        font-size: 14px;
        padding: 0px;
        background: #edeeee;
        border: none;
        color: #53575c;
        text-transform: uppercase;
        width: 97%;
        line-height: 10px;
        min-height: 27px;
        max-height: 10px;
        font-weight: bold;
        margin: 0 auto;
        display: block;

        &.button-follow {
          border: 1px solid $positive;
          background-color: #fff;
          span {
            color: $positive;
            font-size: 14px;
          }

        }

        &.button-unfollow {
          border: 1px solid $positive;
          background-color: $positive;
          span {
            color: #fff;
            font-size: 14px;
          }
        }

      }
    }

    .tabs-light {
      background: #fff;
      height: 50px;
      border-top: 1px solid #ccc;
      border-bottom: 1px solid #ccc;
      display: block;
      position: inherit;

      .tab-nav.tabs {
        .tab-item {
          -webkit-transform: all .5s ease;
          -moz-transform: all .5s ease;
          -ms-transform: all .5s ease;
          -o-transform: all .5s ease;
          transform: all .5s ease;
        }

        .tab-item.tab-item-active, .instagram ion-tabs.tabs-dark.tabs-photogram .tab-item.active, .instagram ion-tabs.tabs-dark.tabs-photogram .tab-item.activated {
          background-color: #fff;
          border: none;
          color: $positive;
        }
      }

    }

  }

  .profile-view {
    display: block;
    overflow: hidden;
    background-color: #f1f1f1;

    .row {
      padding: 0;
      .col {
        padding: 2px;
        margin: 0;
      }
    }
  }
}